import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"

export default {
	components: {cmdIcon},

	data() {
		return {
			activeInput: 0,
			digitalList:[
				{
					num:'1',
					letter:''
				},{
					num:'2',
					letter:'ABC'
				},
				{
					num:'3',
					letter:'DEF'
				},
				{
					num:'4',
					letter:'GHI'
				},
				{
					num:'5',
					letter:'JKL'
				},
				{
					num:'6',
					letter:'MNO'
				},
				{
					num:'7',
					letter:'PQRS'
				},
				{
					num:'8',
					letter:'TUV'
				},
				{
					num:'9',
					letter:'WXYZ'
				},
				{
					num:'',
					letter:''
				},
				{
					num:'0',
					letter:''
				},
				
			] ,
			paymentPwd: ''
		};
	},
	props: {
		show: {
			type: Boolean,
			default: false
		},
		value: {
			type: String,
			default: ''
		},
		digits: {
			type: [Number, String],
			default: 6
		},
		forget: {
			type: Boolean,
			default: true
		},
	},
	computed: {
		payPassWord() {
			var payPassWord = this.paymentPwd.split('') || [];
			payPassWord.fill('*');
			return payPassWord;
		},
		_show() {
			this.initData();
			return String(this.show) === 'false' ? false : true;
		},
		_forget() {
			return String(this.forget) === 'false' ? false : true;
		},
		_digits() {
			let digits = [];
			digits.length = this.digits;
			return digits;
		}
	},
	methods: {
		initData: function() {
			this.paymentPwd = this.value;
			this.activeInput = this.value.length;
		},
		forgetPwd: function() {
			this.$emit('forgetPwd');
			console.log('跳转到忘记密码')
		},
		cancel: function() {
			this.$emit('cancel');
		},
		getKeyNumber: function(val) {
			if (val === '' || (val != -1 && this.activeInput == this.digits)) { //空或者已经达到最大值
				return false;
			} else if (val != -1) { //数字输入
				this.activeInput++
				this.paymentPwd += val;
				if (this.activeInput == this.digits) { //验证密码正确性
					return this.$emit('submit', this.paymentPwd);
				}
			}
		},
		getKeyboard: function(e) {
			var index = e.target.dataset.index;
			if (index === undefined) return false;
			var _length = this.paymentPwd.length
			this.activeInput = index <= _length ? index : _length;
		},
		deleteNum:function () {
			console.log(this.activeInput);
			if (this.activeInput != 0) {
				this.activeInput--;
				this.paymentPwd = this.paymentPwd.substr(0, this.activeInput)
			}
		}
	}
}
